{
	if (params == undefined) params = null;
	Runtime.Widget.Crud.CrudPageModel.call(this, params);
	if (this.layout.route.name == "app:domains:index")
	{
		/* Set action */
		this.setAction("index");
		/* Create table widget */
		var table = new Bayrell.CloudOS.Frontend.Domains.DomainTableModel(Runtime.Map.from({"parent_widget":this}));
		/* Set page */
		table.storage.setPage(this.layout.request_query.get("page", 1));
	}
	else if (this.layout.route.name == "app:domains:edit")
	{
		/* Set action */
		this.setAction("edit");
		/* Create form widget */
		var form = new Bayrell.CloudOS.Frontend.Domains.DomainFormModel(Runtime.Map.from({"action":"edit","parent_widget":this}));
		/* Set primary key for id */
		form.storage.setPrimaryKey(Runtime.Map.from({"id":this.layout.route.matches.get("id")}));
		/* Create routes table widget */
		var route_table = new Bayrell.CloudOS.Frontend.Routes.RouteTableModel(Runtime.Map.from({"parent_widget":this}));
		route_table.storage.setPage(1);
		/* Add table to render list */
		this.getWidget("render_list").addRender(route_table);
		/* Set foreign key */
		var id = this.layout.route.matches.get("id");
		route_table.storage.setForeignKey("domain_id", id);
		route_table.getWidget("form").storage.setForeignKey("domain_id", id);
		route_table.getWidget("form").removeField("domain_id");
	}
};
