Bayrell.CloudOS.Frontend.Templates.TemplatePageModel = function(params)
{
	if (params == undefined) params = null;
	Runtime.Widget.Crud.CrudPageModel.call(this, params);
	if (this.layout.route.name == "app:templates:index")
	{
		/* Set action */
		this.setAction("index");
		/* Create table widget */
		var table = new Bayrell.CloudOS.Frontend.Templates.TemplateTableModel(Runtime.Map.from({"parent_widget":this}));
		/* Set page */
		table.storage.setPage(this.layout.request_query.get("page", 1));
	}
	else if (this.layout.route.name == "app:templates:edit")
	{
		/* Set action */
		this.setAction("edit");
		/* Create form widget */
		var form = new Bayrell.CloudOS.Frontend.Templates.TemplateFormModel(Runtime.Map.from({"action":"edit","parent_widget":this}));
		/* Create versions table widget */
		var version_table = new Bayrell.CloudOS.Frontend.Templates.VersionTableModel(Runtime.Map.from({"parent_widget":this,"widget_name":"version_table"}));
		version_table.storage.setPage(1);
		/* Add table to render list */
		this.getWidget("render_list").addRender(version_table);
		/* Set space id */
		var id = this.layout.route.matches.get("id");
		version_table.storage.setForeignKey("template_id", id);
		version_table.getWidget("form").storage.setForeignKey("template_id", id);
	}
};
