Bayrell.CloudOS.Frontend.Workspace.WorkspacePageModel = function(params)
{
	if (params == undefined) params = null;
	Runtime.Widget.Crud.CrudPageModel.call(this, params);
	if (this.layout.route.name == "app:workspace:index")
	{
		/* Set action */
		this.setAction("index");
		/* Create table widget */
		var table = new Bayrell.CloudOS.Frontend.Workspace.WorkspaceTableModel(Runtime.Map.from({"parent_widget":this}));
		/* Set page */
		table.storage.setPage(this.layout.request_query.get("page", 1));
	}
	else if (this.layout.route.name == "app:workspace:edit")
	{
		/* Set action */
		this.setAction("edit");
		/* Create form widget */
		var form = new Bayrell.CloudOS.Frontend.Workspace.WorkspaceFormModel(Runtime.Map.from({"action":"edit","parent_widget":this}));
		/* Set primary key for id */
		form.storage.setPrimaryKey(Runtime.Map.from({"id":this.layout.route.matches.get("id")}));
		/* Create domains table widget */
		var domain_table = new Bayrell.CloudOS.Frontend.Workspace.DomainTableModel(Runtime.Map.from({"parent_widget":this,"widget_name":"domain_table"}));
		domain_table.storage.setPage(1);
		/* Add table to render list */
		this.getWidget("render_list").addRender(domain_table);
		/* Set foreign key */
		var id = this.layout.route.matches.get("id");
		domain_table.storage.setForeignKey("space_id", id);
		domain_table.getWidget("form").storage.setForeignKey("space_id", id);
	}
};
